import React from 'react'
import { app, StoreWrap } from 'xadmin-core'

import i18n from 'xadmin-i18n'
import form, {
  SchemaForm
} from 'xadmin-form'

@StoreWrap('hello')
class Hello extends React.Component {

  onSubmit(values) {
    const { count, add } = this.props
    console.log(values)
    add(values)
  }

  render() {
    const { count, add } = this.props
    const { _t } = app.context

    const FormLayout = (props) => {
      const { children, invalid, handleSubmit, submitSucceeded, submitting, isCreate } = props
      return (
        <form className="form-horizontal">
          {children}
          <a onClick={handleSubmit} >{_t('Add Count')}</a>
        </form>
      )
    }

    return (
      <div>
        {count} 
      </div>
    )
  }
}

app
.use(i18n)
.use(form)
.use({
  config: {
    locale: {
      lng: 'zh_Hans'
    }
  },
  locales: {
    zh_Hans: {
      translation: {
        'Add Count': '添加数字'
      }
    }
  },
  mappers: {
    hello: {
      data: ({ state }, props ) => {
        return { count: state.count }
      },
      method: {
        add: ({ dispatch }) => (values) => {
          dispatch({ type: 'ADD', ...values })
        }
      }
    }
  },
  reducers: {
    count: (state=0, action) => {
      if(action.type == 'ADD') {
        return state + action.count
      }
      return state
    }
  },
  routers: {
    '@': {
      path: '/',
      component: Hello 
    }
  }
}).start({ container: '#app' })



